.form-container h3 {
    margin-top: 0;
}

.cm-form-field {
    margin-bottom: 20px;
}

.cm-form-field label {
    position: relative;
    display: block;
    color: #222222;
    margin-bottom: 3px;
}

.cm-form-field input[type="text"],
.cm-form-field input[type="email"],
.cm-form-field textarea {
    display: block;
    font-family: overpass,Arial,sans-serif;
    background: #fff;
    font-size: 16px;
    line-height: 1.8;
    padding: 10px 20px;
    border: 1px solid #e5e5e5;
    margin-bottom: 12px;
    transition: all 500ms ease;
    width: 100%;
}

.cm-form-field textarea {
    min-height: 150px;
}

.cm-form-field input[type="text"]:focus,
.cm-form-field input[type="email"]:focus,
.cm-form-field textarea:focus {
    border: 1px solid #ff5e14;
}

.cm-submit {
    display: inline-block;
    font-size: 18px;
    padding: 12px 49px 12px 49px;
    background: #ff5e14;
    color: #ffffff;
    border-radius: 30px;
    text-transform: capitalize;
    transition: all 300ms ease;
    cursor: pointer;
    text-align: center;
    z-index: 1;
    border: none;
    font-family: overpass,Arial,sans-serif;
    line-height: 1.8;
    text-transform: uppercase;
    margin-top: 20px;
}

.cm-submit:hover {
    background: #212121;
    color: #ffffff;
}